import NiceModal, { useModal, antdModal } from "@ebay/nice-modal-react";
import { Form, Input, Modal, Select, Upload } from "antd";
import { useStore } from "entities/store";
import { useCallback, useState } from "react";
import { IQuestion, IQuestionType } from "./types";

const { TextArea } = Input;
const { Dragger } = Upload;
const { Option } = Select;

export default NiceModal.create(({ question }: { question: IQuestion }) => {
  console.log("🚀 ~ file: question.tsx ~ line 11 ~ NiceModal.create ~ tip", question)

  const [tip, setTip] = useState<IQuestionType | null>(null);
  console.log("🚀 ~ file: question.tsx ~ line 13 ~ NiceModal.create ~ tip", tip)

  const modal = useModal();
  const [form] = Form.useForm();

  const { question_type } = useStore((state) => state.catalogs);

  const handleSubmit = useCallback(() => {
    form.validateFields().then(() => {
      const newQuestion = { ...form.getFieldsValue() };
      modal.resolve(newQuestion);
      modal.hide();
    });
  }, [
    modal,
    // user,
    form,
  ]);

  const onTypeQuestionChange = (value: any) => {
    const nameOfQuestionType = question_type.find(
      (question: any) => question.id === value
    );
    console.log(
      "🚀 ~ file: question.tsx ~ line 36 ~ onTypeQuestionChange ~ nameOfQuestionType",
      nameOfQuestionType
    );
    setTip(nameOfQuestionType)
    form.setFieldsValue({ name: nameOfQuestionType.name });
  };

  return (
    <Modal
      {...antdModal(modal)}
      title="Вопрос повестки"
      //   okText={user ? "Update" : "Create"}
      onOk={handleSubmit}
      okText="Сохранить"
      cancelText="Отмена"
    >
      <Form 
      form={form} 
      layout="vertical"
       initialValues={question} 

       >
        <Form.Item
          name="question_type_id"
          label="Тип вопроса"
          rules={[
            {
              required: true,
              message: "Please input the title of collection!",
            },
          ]}
        >
          <Select
            allowClear
            style={{ width: "100%" }}
            onChange={onTypeQuestionChange}
            showSearch
            filterOption={(input, option) =>
              (option!.children as unknown as string)
                .toLowerCase()
                .includes(input.toLowerCase())
            }
          >
            {question_type.map((item: any) => (
              <Option key={item.id} value={item.id}>
                {item.name}
              </Option>
            ))}
          </Select>
        </Form.Item>

        <Form.Item
          name="name"
          label="Вопрос"
          rules={[
            {
              required: true,
              message: "Please input the title of collection!",
            },
          ]}
        >
          <TextArea />
        </Form.Item>
        <Form.Item
          name="solution"
          label="Предложенное решение"
          rules={[
            {
              required: true,
              message: "Please input the title of collection!",
            },
          ]}
        >
          <TextArea />
        </Form.Item>
        <Form.Item
          name="description"
          label="Пояснения"
          rules={[
            {
              // required: true,
              message: "Please input the title of collection!",
            },
          ]}
        >
          <TextArea rows={8} />
        </Form.Item>

        {/* <Form.Item
          name="files"
          label="Дополнительные файлы"

        >
          <Dragger 
          >
            <p className="ant-upload-drag-icon">
            </p>
            <p className="ant-upload-text">
              Загрузите необходимые документы
            </p>
            <p className="ant-upload-hint">
              Перенесите нужные файлы или нажмите на эту кнопку
            </p>
          </Dragger>
        </Form.Item> */}
      </Form>
      <div>{tip?.description}</div>
    </Modal>
  );
});
